.App {
  text-align: center;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.App-header {
  background-color: #282c34;
  /* background-color: black; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
}

/* .App-link {
  color: #61dafb;
} */

#HeaderPage, #NavPage, #InfoPage{
  background-image: url('../public/img/BGimg/graybg.jpg');
  background-size: cover;
}
button {
  border-radius: 5px;
  background: transparent;
  color: white;
  transition-duration: 0.4s;

}
button:hover{
   background-color: rgba(255, 255, 255);
   opacity: 0.8;
  color: black;
}



@media (min-width: 768px){

.about{
  display:flex;
  justify-content: space-evenly;
  align-items: center;
}

.aboutText{
  height: 50%;
  width: 50%;
}
.kevinImgBottom{
  display: none
  }

.FAQText{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 150px ;
  margin-right: 150px;
}

#body{
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 30vh 10vh 60vh;
  grid-template-areas:
  'header'
  'nav'
  'info'
}

.logo{
  height: 100%;
  width:30%;
}

}




@media (min-width: 200px) and (max-width: 767px){

  .logo{
    height: 100%;
    width:30%;
  }
  .about{
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  .aboutText{
    margin-left: 50px ;
    margin-right: 50px;
  }
.kevinImgTop{
  display: none;
}

.FAQText{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 50px ;
  margin-right: 50px;
}

#body{
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 20vh 10vh 70vh;
  grid-template-areas:
  'header'
  'nav'
  'info'
}
}